import React, { Component } from 'react';
import { Card, Radio, Icon, Button,Tooltip } from 'antd';
import DownloadChart from '../BizCharts/DownloadChart'
import ReactHTMLTableToExcel from '../TableToExcel';

class MainCard extends Component {
    handleMingxi=()=>{
        this.props.detailCallback()
    }
    render () {
        return (
            <Card
                className={this.props.className}
                headStyle={{ border: "none" }}
                title={<div style={{lineHeight:'34px'}}>{this.props.title}</div>}
                extra={this.props.extra || <React.Fragment>
                    <Radio.Group value={this.props.tab || '1'} style={{marginRight:10}} onChange={this.props.changeTab} >
                    <Tooltip placement="top" title='图表'>
                        <Radio.Button value="1"><Icon type="bar-chart" theme="outlined" /></Radio.Button>
                    </Tooltip>
                    <Tooltip placement="top" title='表格'>
                        <Radio.Button value="2"><Icon type="table" theme="outlined" /></Radio.Button>
                    </Tooltip>                       
                        
                    </Radio.Group>
                    {this.props.tab==='1'?(
                        <DownloadChart {...(this.props.downloadChart || {})}>
                            <Tooltip placement="top" title='导出'>
                                <Button theme="outlined" style={{padding:"0px 8px"}}>
                                    <i className="iconfont icon-daochu1" />
                                </Button>
                            </Tooltip>
                                                
                        </DownloadChart>
                    ):(
                        <ReactHTMLTableToExcel
                            {...(this.props.tableToExcel || {})}
                            content={
                                <Tooltip placement="top" title='导出'>
                                    <Button theme="outlined" style={{padding:"0px 8px"}}>
                                        <i className="iconfont icon-daochu1" />
                                    </Button>
                                </Tooltip>} />
                    )}
                    {
                        this.props.detailCallback ? <Tooltip placement="top" title='导出明细'>
                            <Button onClick={this.handleMingxi} style={{marginLeft:10,padding:"0px 8px"}} theme="outlined">
                                <i className="iconfont icon-daochu" />
                            </Button>
                        </Tooltip> : null
                    }
                        
                </React.Fragment>}
            >
                {this.props.children}
            </Card>
        )
    }
}

export default MainCard;


