import React, {Component} from 'react';
import {Table} from 'antd';
import {getLocal} from "../../../../../utils/index";
import {postAction} from "../../../../../axios";
import MainCard from "../../../../../common/components/MainCard/index";
import ReactDOM from "react-dom";
import {hostUrl} from "../../../../../env";
import BarLineChart from './BarLineChart';


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '学期',
    dataIndex: 'semester',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '专业平均（柱状）',
    dataIndex: 'majAvg'
},{
    title:'个人（柱状）',
    dataIndex:'oneAvg'
}, {
    title: '专业平均（折线）',
    dataIndex: 'majGpa'
},{
    title:'个人（折线）',
    dataIndex:'oneGpa'
}];

export default class LiShiChengJi extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            lineData: [],
            tableData : [],
            pagination: {
                pageSize:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
    }

    getHistoryData () {   // 获取个人历史成绩分析
        let _studentNo = JSON.parse(getLocal('jzzz'));
        postAction(
            "/bigdata/studentScoresWeight/queryStudentStudyByStudentNo",
            {
                studentNo: _studentNo.studentNo,
                majorCode: _studentNo.majorCode
            }
        ).then((res) => {
            if (res.success && res.obj.length > 0) {

                let _lineData = [];
                let _tableData = [];

                res.obj.forEach(
                    (item, index) => {

                        _lineData.push({
                            name: item.semesterYear,
                            "专业平均（柱状）": item.majAvg ? Number(Math.round(item.majAvg*100)/100) : 0,
                            "个人（柱状）": item.oneAvg ? Number(Math.round(item.oneAvg*100)/100) : 0,
                            "专业平均（折线）": item.majGpa ? Number(Math.round(item.majGpa*100)/100) : 0,
                            "个人（折线）": item.oneGpa ? Number(Math.round(item.oneGpa*100)/100) : 0
                        })

                        _tableData.push({
                            key: index + 1,
                            sortIndex: index + 1,
                            semester: item.semesterYear,
                            majAvg: item.majAvg ? Number(Math.round(item.majAvg*100)/100) : 0,
                            oneAvg: item.oneAvg ? Number(Math.round(item.oneAvg*100)/100) : 0,
                            majGpa: item.majGpa ? Number(Math.round(item.majGpa*100)/100) : 0,
                            oneGpa: item.oneGpa ? Number(Math.round(item.oneGpa*100)/100) : 0,
                        })
                    }
                )
                this.setState({
                    lineData: _lineData,
                    tableData: _tableData,
                    pagination: {
                        pageSize:5, // 每页条数
                        showQuickJumper: true,
                        total: res.obj.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    lineData:[],
                    tableData:[],
                    pagination: {
                        pageSize: 5, // 每页条数
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'student-xls')
            }
        })
    }

    //导出明细
    getDetail () {
        let _student = JSON.parse(getLocal('jzzz')).studentNo;
        window.location.href=`${hostUrl}/bigdata/studentScoresWeight/exportStudentScore?studentNo=${_student}&reorder=3`;
    }

    render () {
        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">个人历史成绩分析
                    <span>单位：分</span>
                    <span style={{float:"right",color:"#4a4a4a",fontSize:"14px",marginRight:"10px"}}>总人数：
                        <span style={{fontSize:"16px",color:"#1890ff"}}>{this.state.total}</span>
                    </span>
                </div>}
                tab={this.state.tab}
                detailCallback={this.getDetail.bind(this)}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "个人历史成绩分析"
                }}
                tableToExcel={{
                    table: 'student-xls',
                    filename: '个人历史成绩分析excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                        <BarLineChart
                            data={this.state.lineData}
                            LineDataKey={["专业平均（折线）","个人（折线）"]}
                            padding={[10, 40, 60, 60]}
                            height={this.props.height - 110}
                            onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                            lineScale={{
                                '专业平均（折线）':{minLimit:0,ticks:[0,1,2,3,4,5]},
                                '个人（折线）':{minLimit:0,ticks:[0,1,2,3,4,5]}
                            }}
                            geomType="intervalStack">
                        </BarLineChart>
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                                pagination={this.state.pagination}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount (){
        this.getHistoryData();  // 获取个人历史成绩分析
    }

}


