import React, {Component, Fragment} from 'react';
import {Table} from 'antd';
import PieChart from '../../../../common/components/BizCharts/PieChart';
import {postAction} from "../../../../axios";
import {getLocal} from "../../../../utils";
import MainCard from "../../../../common/components/MainCard";
import ReactDOM from "react-dom";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '消费类型',
    dataIndex: 'type',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '消费金额',
    dataIndex: 'money'
}];

export default class XiaoFeiJieGou extends Component {

    constructor(props){
        super(props);
        this.state = {
            engel:0,
            tab: '1',
            chartIns: undefined,
            pieData: [
                // { item: '事例一', count: 40 }
            ],
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                pageSize:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getPieDate = this.getPieDate.bind(this);     // 获取消费结构分析
    }

    getPieDate () {    // 获取消费结构分析
        let _studentNo = JSON.parse(getLocal('jzzz')).studentNo;
        postAction(
            "/bigdata/povertyAnalyze/getConsumeStructure",
            {
                studentNo:_studentNo
            }
        ).then((res) => {
            if (res.success && res.obj.detail.length > 0) {
                let dataObj = [];
                let newTableData = [];
                res.obj.detail.forEach(
                    (item,index) => {
                        dataObj.push({
                            item: item.name,
                            count: Math.round(item.value)
                        })
                        newTableData.push({
                            key: index + 1,
                            sortIndex: index + 1,
                            type: item.name,
                            money: Math.round(item.value),
                        })
                    });
                this.setState({
                    pieData: dataObj,
                    tableData:newTableData,
                    engel:Number(res.obj.engel.engel) * 100,
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: res.obj.detail.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    pieData: [],
                    engel:0,
                    tableData: [],
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'three-xls')
            }
        })
    }


    render () {
        return (

            <MainCard
                className="main-card"
                title={<div className="common-title">近三个月消费结构
                    <span>单位：元</span>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "近三个月消费结构"
                }}
                tableToExcel={{
                    table: 'three-xls',
                    filename: '近三个月消费结构excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?
                <Fragment>
                    <PieChart
                        padding={[0, 0, 60, 0]}
                        height={this.props.height - 70}
                        data={this.state.pieData}
                        colors = {["#3aa1ff","#5ddfcf","#4ecb73","#fbd437","#eaa674","#8a7bd4","#975fe5","#749fea","#36cbcb","#74b1ea","#5254cf","#435188"]}
                        onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                    />
                    <div style={{textAlign:"center",color:"rgba(0,0,0,0.75)"}}>近3个月的恩格尔系数为{this.state.engel}%</div>
                </Fragment>
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                                pagination={this.state.pagination}
                                style={{height:350}}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount (){
        this.getPieDate();      // 获取消费结构分析
    }

}


