import React, { Component} from 'react';
import { Row, Col, Card,Button } from 'antd';
import {withRouter} from 'react-router-dom'
import {getLocal} from '../../../../utils'
import '../../index.less';
import {postAction} from "../../../../axios";
import GeRenXiaoFei from "./GeRenXiaoFei";
import LiShiChengJi from "./LiShiChengJi/LiShiChengJi";
import XiaoFeiJieGou from "./XiaoFeiJieGou";
import ZiZhuJinE from "./ZiZhuJinE";


// const Option = Select.Option;
class Detail extends Component {

    constructor(props){
        super(props);
        this.state = {
            year: '',
            yearList: ['2016', '2017', '2018'],
            detail: [
            ],

        }
        this.getdDetailData = this.getdDetailData.bind(this);  // 获取基础信息
    }

    getdDetailData() {   // 获取基础信息
        let _student = JSON.parse(getLocal('jzzz'));
        postAction(
            "/bigdata/povertyAnalyze/povertyStudentInfo",
            {
                studentNo:_student.studentNo,
                schoolYear:_student.filterValue.curYear
            }
        ).then((res) => {
            if (res.success){                
                this.setState({
                    detail:[
                        {label: '姓名:',value: res.obj.studentName === undefined ? "" : res.obj.studentName},
                        {label: '学号:',value: res.obj.studentId === undefined ? "" : res.obj.studentId},
                        {label: '性别:',value: res.obj.gender === undefined ? "" : res.obj.gender},
                        {label: '学院:',value: res.obj.collegeName === undefined ? "" : res.obj.collegeName},
                        {label: '专业:',value: res.obj.majorName === undefined ? "" : res.obj.majorName},
                        {label: '经济困难原因:',value: res.obj.povertyReason === undefined ? "" : res.obj.povertyReason},
                        {label: '认定等级:',value: res.obj.povertyLevel === undefined ? "" : res.obj.povertyLevel},
                        {label: '最近一次资助金额:',value: res.obj.lastSubsidize === undefined ? "" : res.obj.lastSubsidize+'元'},
                        {label: '最近一次资助时间:',value: res.obj.lastSubsidizeDate === undefined ? "" : res.obj.lastSubsidizeDate},
                        {label: `${res.obj.schoolYear}学年资助金额:`,value: res.obj.schoolYearSubsidize === undefined ? "" : res.obj.schoolYearSubsidize+'元'},
                        {label: `累计到${res.obj.schoolYear}学年历史资助总金额:`,value: res.obj.totalSubsidize === undefined ? "" : res.obj.totalSubsidize+'元'}
                    ]
                })
            } else {
                this.setState({
                    detail:[]
                })
            }
        })
    }





	componentDidMount (){
        this.getdDetailData();  // 获取基础信息
    }

    backtolist=()=>{
        // 返回之前点击的列表页面
        const jzzz=JSON.parse(getLocal('jzzz'))
        this.props.history.push(jzzz.backUrl)
    }

    render() {

        return (
            <div className="pkdetail">
                
				{/* <div className="topSearch">
					<Select value={this.state.year} style={{ width: 120 }} onChange={this.handleYearChange}>
						<Option value="">全部</Option>
						{this.state.yearList.map(item => <Option key={item} value={item}>{item}</Option>)}
					</Select>
                </div> */}
                <Card
                    headStyle={{border:"none"}}
                    title={<div className="common-title">家庭经济困难学生详情</div>}
                    extra={
                        <Button type='primary' onClick={this.backtolist}>返回</Button>
                    }
                >
                    <Row style={{backgroundColor: '#fff',padding: '10px 20px'}}>

                    {
                        this.state.detail.map(
                            item => <Col span={6} key={item.label} style={{marginBottom:5, paddingRight:20}}>
                                        <span className="label">{item.label}</span>
                                        <span className="value">{item.value}</span>
                                    </Col>
                        )
                    }
                    </Row>
                </Card>
				<Row  className="section">
					<Col span={12} style={{paddingRight:'10px'}}>
						<GeRenXiaoFei/>
					</Col>
                    <Col span={12} style={{paddingLeft:'10px'}}>
                        <ZiZhuJinE/>
                    </Col>
				</Row>
				<Row className="section" gutter={20}>
					<Col span={12}>
						<LiShiChengJi/>
					</Col>
					<Col span={12}>
						<XiaoFeiJieGou height={400}/>
					</Col>
				</Row>
			</div>
        );
    }
}

export default withRouter(Detail);


