import React, {Component} from 'react';
import {Table} from 'antd';

import BarChart from '../../../../common/components/BizCharts/BarChart';
import MainCard from "../../../../common/components/MainCard";
import ReactDOM from "react-dom";
import {postAction} from "../../../../axios";
import {getLocal} from "../../../../utils";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '学年',
    dataIndex: 'year',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '资助金额',
    dataIndex: 'money'
}];

export default class ZiZhuJinE extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            data : [
                // {
                //     year: "1951 年",
                //     sales: 38
                // }
            ],
            tableData : [
                // { key: '1', sortIndex:'1', year: '2018', money: 165 }
            ],
            pagination: {
                pageSize:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
    }

    getData () {
        let _studentNo = JSON.parse(getLocal('jzzz')).studentNo;
        postAction(
            "/bigdata/povertyAnalyze/schoolYearSubsidizeList",
            {
                "studentNo":_studentNo
            }
        ).then(
            (res) => {
                if (res.success) {
                    let newData = [];
                    let newTable = [];

                    res.obj.forEach(
                        (item, index) => {

                            newData.push({
                                year: item.schoolYear + "学年",
                                "资助金额": item.subsidize
                            })

                            newTable.push({
                                key: index + 1,
                                sortIndex: index + 1,
                                year: item.schoolYear,
                                money: item.subsidize
                            })
                        }
                    )
                    this.setState({
                        data:newData,
                        tableData: newTable,
                        pagination: {
                            pageSize: 5,
                            showQuickJumper: true,
                            total: res.obj.length,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                } else {
                    this.setState({
                        data:[],
                        tableData: [],
                        pagination: {
                            pageSize: 5,
                            showQuickJumper: true,
                            total: null,
                            showTotal: (total) => {
                                return (
                                    `共${total}条数据`
                                )
                            }
                        }
                    })
                }
            }
        )
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'jine-xls')
            }
        })
    }

    render () {
        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">各学年资助金额
                    <span>单位：元</span>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "各学年资助金额"
                }}
                tableToExcel={{
                    table: 'jine-xls',
                    filename: '各学年资助金额excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?

                        <BarChart
                            data={this.state.data}
                            height={350}
                            padding={[10,40,60,50]}
                            onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}

                        />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                                pagination={this.state.pagination}
                                style={{height:350}}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount (){
        this.getData();
    }

}


