import React, {Component} from 'react';
import {Table} from 'antd';
import MainCard from "../../../../common/components/MainCard";
import LineChart from '../../../../common/components/BizCharts/LineChart';
import {postAction} from "../../../../axios";
import {getLocal} from "../../../../utils";
import ReactDOM from "react-dom";


const columns = [{
    title:'序号',
    dataIndex: 'sortIndex',
    width: '10%',
},{
    title: '月份',
    dataIndex: 'month',
    render: (text) => {
        return (
            <span title={text}>{text}</span>
        )
    }
}, {
    title: '资助金额',
    dataIndex: 'subsidize'
}, {
    title: '消费金额',
    dataIndex: 'consume'
}];

export default class GeRenXiaoFei extends Component {
    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            lineData1: [
                // { month: "Jan", "资助金额": 7.0, "消费金额": 3.9 }
            ],
            tableData : [
                // { key: '1', sortIndex:'1', college: '建筑学院', number: 165, rate: '5%' }
            ],
            pagination: {
                pageSize:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `共${total}条数据`
                    )
                }
            }
        }
        this.getLineData = this.getLineData.bind(this);   // 获取个人消费金额和资助金额趋势分析
    }

    getLineData () {   // 获取个人消费金额和资助金额趋势分析
        let _student = JSON.parse(getLocal('jzzz'));
        postAction(
            "/bigdata/povertyAnalyze/consumeAndSubsidize",
            {
                studentNo:_student.studentNo,
                schoolYear:_student.filterValue.curYear
            }
        ).then((res) => {
            if (res.success){
                let newData = [];
                let newTableData = [];

                res.obj.forEach(
                    (item, index) => {

                        newData.push({
                            month: item.month + "月",
                            "资助金额": item.subsidize,
                            "消费金额": item.consume
                        })

                        newTableData.push({
                            key: index + 1,
                            sortIndex: index + 1,
                            month: item.month + "月",
                            subsidize: item.subsidize,
                            consume: item.consume
                        })
                    }
                )
                this.setState({
                    lineData1: newData,
                    tableData:newTableData,
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: res.obj.length,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            } else {
                this.setState({
                    lineData1: [],
                    tableData: [],
                    pagination: {
                        pageSize: 5,
                        showQuickJumper: true,
                        total: null,
                        showTotal: (total) => {
                            return (
                                `共${total}条数据`
                            )
                        }
                    }
                })
            }
        })
    }

    //tab切换
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'geren-xls')
            }
        })
    }


    render () {

        return (
            <MainCard
                className="main-card"
                title={<div className="common-title">个人消费金额和资助金额趋势分析
                    <span>单位：元</span>
                </div>}
                // detailCallback={this.getDetail.bind(this)}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title: "个人消费金额和资助金额趋势分析"
                }}
                tableToExcel={{
                    table: 'geren-xls',
                    filename: '个人消费金额和资助金额趋势分析excel下载'
                }}
            >
                {
                    this.state.tab === '1'
                        ?

                <LineChart
                    data={this.state.lineData1}
                    onFirstLoad={(chartIns) => { this.setState({ chartIns }) }}
                />
                        :
                        <React.Fragment>
                            <Table
                                dataSource={this.state.tableData}
                                columns={columns}
                                bordered
                                size="small"
                                pagination={this.state.pagination}
                                style={{height:350}}
                            />

                            <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                        </React.Fragment>
                }
            </MainCard>
        )
    }

    componentDidMount (){
        this.getLineData();     // 获取个人消费金额和资助金额趋势分析
    }
}


